<template>
  <div>
    <navbar-zakat />
    <div class="container">
      <h1 class="mt-5">Formulir Input Fidyah</h1>
      <form @submit.prevent="postData" class="mt-4">
        <div class="form-group">
          <label for="nama_lengkap">No Tim Input : </label>
          <select
            class="form-select p2"
            id="nama_lengkap"
            v-model="formData.tim"
          >
            <option disabled value="">Pilih No Tim</option>
            <option value="1">Tim 1</option>
            <option value="2">Tim 2</option>
            <option value="3">Tim 3</option>
            <option value="4">Tim 4</option>
            <option value="5">Tim 5</option>
            <option value="6">Tim 6</option>
            <option value="7">Tim 7</option>
          </select>
        </div>

        <div class="form-group">
          <label for="nama">Nama:</label>
          <input
            type="text"
            id="nama"
            v-model="formData.nama"
            class="form-control"
          />
        </div>

        <div class="form-group">
          <label for="alamat">Alamat:</label>
          <input
            type="text"
            id="alamat"
            v-model="formData.alamat"
            class="form-control"
          />
        </div>

        <div class="form-group">
          <label for="nomor_telefon">Nomor Telepon:</label>
          <input
            type="text"
            id="nomor_telefon"
            v-model="formData.nomor_telefon"
            class="form-control"
          />
        </div>

        <div class="form-group">
          <label for="bin">Bin:</label>
          <input
            type="text"
            id="bin"
            v-model="formData.bin"
            class="form-control"
          />
        </div>

        <div class="form-group">
          <label for="binti">Binti:</label>
          <input
            type="text"
            id="binti"
            v-model="formData.binti"
            class="form-control"
          />
        </div>

        <div class="form-group">
          <label for="Jumlah_fidyah">Jumlah Fidyah:</label>
          <input
            type="text"
            id="Jumlah_Fidyah"
            v-model="formData.jumlah_fidyah"
            class="form-control"
          />
        </div>

        <button type="submit" class="btn btn-primary">Submit</button>
      </form>
    </div>
  </div>
</template>

<script>
import NavbarZakat from "../components/NavbarZakat.vue";
import { getCookie } from "../cookieUtils.js";

export default {
  components: { NavbarZakat },
  data() {
    return {
      formData: {
        nama: "",
        alamat: "",
        nomor_telefon: "",
        bin: "",
        binti: "",
        jabatan_di_keluarga: "",
        uuid_family: "99",
        uuid_masjid: "42274315",
        jumlah_fidyah: "0",
        tim:"",
      },
    };
  },
  mounted() {
    const token = getCookie("token");
    if (!token) {
      this.$router.push("/login");
    }
  },
  methods: {
    async postData() {
      try {
        // Kirim data ke API
        const response = await fetch(
          "http://103.155.117.238:5000/input-zakat/input-fidyah/",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(this.formData),
          }
        );

        // Handle response dari API
        const responseData = await response.json();
        console.log(responseData); // Tampilkan response dari API (opsional)

        // Reset formulir setelah pengiriman berhasil
        if (responseData.status !== "ok") {
          alert("Gagal menyimpan data " + responseData.message);
        } else {
          alert("Berhasil Menyimpan Data");
        }
        this.resetForm();
      } catch (error) {
        alert("Gagal menyimpan data " + error);
        console.error("Error:", error);
      }
    },
    resetForm() {
      // Reset nilai formulir
      this.formData = {
        nama: "",
        alamat: "",
        nomor_telefon: "",
        bin: "",
        binti: "",
        jabatan_di_keluarga: "",
        uuid_family: "99",
        uuid_masjid: "42274315",
        jumlah_fidyah: "0",
        tim:"",
      };
    },
  },
};
</script>

<style scoped>
/* Styles khusus untuk komponen ini */
</style>
