<template>
  <div>
    <NavbarZakat />
    <div class="container">
      <h2>Data Fidyah</h2>
      <div class="row">
        <div class="col-md-6">
          <div class="mb-3">Total tim 1 : {{ fidyahTim.totalFidyahTim1 }}</div>
          <div class="mb-3">Total tim 2 : {{ fidyahTim.totalFidyahTim2 }}</div>
          <div class="mb-3">Total tim 3 : {{ fidyahTim.totalFidyahTim3 }}</div>
        </div>
        <div class="col-md-6">
          <div class="mb-3">Total tim 4 : {{ fidyahTim.totalFidyahTim4 }}</div>
          <div class="mb-3">Total tim 5 : {{ fidyahTim.totalFidyahTim5 }}</div>
          <div class="mb-3">Total tim 6 : {{ fidyahTim.totalFidyahTim6 }}</div>
          <div class="mb-3">Total tim 7 : {{ fidyahTim.totalFidyahTim7 }}</div>
        </div>
      </div>
      <!-- Pencarian -->
      <div class="input-group mb-3">
        <input
          type="text"
          class="form-control"
          placeholder="Cari berdasarkan nama atau jumlah Fidyah"
          v-model="searchQuery"
          @input="search"
        />
        <button
          class="btn btn-outline-secondary"
          type="button"
          @click="clearSearch"
        >
          Bersihkan
        </button>
      </div>

      <!-- Tombol Print -->
      <button class="btn btn-primary" @click="printPage">Print</button>

      <!-- Tabel -->
      <table class="table" id="tabelFidyah">
        <thead>
          <tr>
            <th scope="col">No</th>
            <th scope="col">UUID</th>
            <th scope="col">Nama</th>
            <th scope="col">Jumlah Fidyah</th>
            <th scope="col">Tim</th>
            <th scope="col">Action</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(fidyah, index) in paginatedFidyahData" :key="fidyah.uuid">
            <td>{{ (currentPage - 1) * pageSize + index + 1 }}</td>
            <td>{{ fidyah.uuid }}</td>
            <td>{{ fidyah.nama }}</td>
            <td>{{ fidyah.jumlahFidyah }}</td>
            <td>{{ fidyah.tim ? fidyah.tim : "-" }}</td>
            <td class="action-column" v-if="!printing">
              <button class="btn btn-danger" @click="hapusItem(fidyah.uuid)">
                Hapus
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- Paginasi -->
      <nav aria-label="Page navigation">
        <ul class="pagination">
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <a class="page-link" href="#" @click.prevent="prevPage">Previous</a>
          </li>
          <li
            class="page-item"
            v-for="pageNumber in totalPages"
            :key="pageNumber"
            :class="{ active: pageNumber === currentPage }"
          >
            <a
              class="page-link"
              href="#"
              @click.prevent="goToPage(pageNumber)"
              >{{ pageNumber }}</a
            >
          </li>
          <li
            class="page-item"
            :class="{ disabled: currentPage === totalPages }"
          >
            <a class="page-link" href="#" @click.prevent="nextPage">Next</a>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { getCookie } from "../cookieUtils.js";
import NavbarZakat from "@/components/NavbarZakat.vue";

export default {
  name: "TabelFidyah",
  components: {
    NavbarZakat,
  },

  data() {
    return {
      fidyahData: [],
      fidyahTim: [],
      searchQuery: "",
      currentPage: 1,
      pageSize: 5, // Jumlah item per halaman
      printing: false,
    };
  },
  computed: {
    filteredFidyahData() {
      return this.fidyahData.filter((fidyah) => this.filterFidyah(fidyah));
    },
    totalPages() {
      return Math.ceil(this.filteredFidyahData.length / this.pageSize);
    },
    paginatedFidyahData() {
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = startIndex + this.pageSize;
      return this.filteredFidyahData.slice(startIndex, endIndex);
    },
  },
  mounted() {
    const token = getCookie("token");
    if (!token) {
      this.$router.push("/login");
    }
    this.fetchData();
  },
  methods: {
    fetchData() {
      axios
        .get("http://103.155.117.238:5000/input-zakat/search/fidyahAll/")
        .then((response) => {
          this.fidyahData = response.data.data.data;
          this.fidyahTim = response.data.data;
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
        });
    },
    hapusItem(uuid) {
      if (confirm("Apakah Anda yakin ingin menghapus entri ini?")) {
        axios
          .delete(
            `http://103.155.117.238:5000/input-zakat/search/fidyahDelete/${uuid}`
          )
          .then((response) => {
            console.log("Berhasil menghapus entri:", response.data);
            // Update data setelah menghapus
            this.fetchData();
          })
          .catch((error) => {
            console.error("Error deleting entry:", error);
          });
      }
    },
    search() {
      // Mengaktifkan pencarian saat nilai input berubah
      // Di sini Anda bisa menambahkan logika pencarian sesuai kebutuhan
    },
    clearSearch() {
      // Membersihkan query pencarian dan mereset data
      this.searchQuery = "";
      this.fetchData();
    },
    filterFidyah(fidyah) {
      // Logika pencarian berdasarkan nama dan jumlah Fidyah
      const searchQueryLower = this.searchQuery.toLowerCase();
      return (
        fidyah.nama.toLowerCase().includes(searchQueryLower) ||
        fidyah.jumlahFidyah.toString().includes(searchQueryLower)
      );
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    goToPage(pageNumber) {
      // Pastikan nomor halaman berada dalam rentang yang valid
      if (pageNumber >= 1 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },
    printPage() {
      // Set printing ke true saat mencetak
      this.printing = true;

      // Tangkap konten tabel
      let printContents = document.getElementById("tabelFidyah");

      // Simpan konten HTML asli
      let originalContents = document.body.innerHTML;

      // Jika konten tabel ditemukan
      if (printContents) {
        // Ambil konten tabel
        printContents = printContents.outerHTML;

        // Set konten body HTML ke konten tabel
        document.body.innerHTML = printContents;

        // Lakukan pencetakan
        window.print();

        // Set konten body HTML kembali ke konten asli
        document.body.innerHTML = originalContents;

        // Set printing kembali ke false setelah selesai mencetak
        this.printing = false;
      } else {
        console.error("Element with id 'tabelFidyah' not found.");
      }
    },
  },
};
</script>

<style>
/* Tambahkan gaya CSS sesuai kebutuhan */
</style>
