<template>
  <div class="Container">
    <NavbarZakat />
    <div class="container">
      <div
        class="container d-flex justify-content-center align-items-center vh-100"
      >
        <div>
          <h2 class="mb-4">Data Zakat</h2>
          <p>
            Jumlah keluarga membayar zakat:
            {{ kesimpulan.jumlahKeluargaYangMembayarZakat }} keluarga
          </p>
          <p>
            Jumlah pembayar zakat : {{ kesimpulan.jumlahPembayarZakat }} Orang
          </p>

          <p>Jumlah pembayar fidyah : {{ kesimpulan.jumlahPembayarFidyah }}</p>

          <p>
            Jumlah nominal seluruh zakat terkumpul : Rp.
            {{ kesimpulan.jumlahSeluruhZakat }}
          </p>
          <p>
            Jumlah nominal seluruh sodakoh terkumpul : Rp.
            {{ kesimpulan.jumlahSeluruhSodakoh }}
          </p>
          <p>
            Jumlah nominal seluruh fidyah terkumpul : Rp.
            {{ kesimpulan.jumlahSeluruhfidyah }}
          </p>
          <p>
            Jumlah nominal seluruhnya: Rp.
            {{ kesimpulan.jumlahSeluruhDanaZakatSodakohDanFidyah }}
          </p>
        </div>
      </div>

      <div class="container">
        
        <div class="row">
          <div class="col-md-6">
            <div class="mb-3">
              Total tim 1 : {{ kesimpulan.totalZakatTim1 }}
            </div>
            <div class="mb-3">
              Total tim 2 : {{ kesimpulan.totalZakatTim2 }}
            </div>
            <div class="mb-3">
              Total tim 3 : {{ kesimpulan.totalZakatTim3 }}
            </div>
          </div>
          <div class="col-md-6">
            <div class="mb-3">
              Total tim 4 : {{ kesimpulan.totalZakatTim4 }}
            </div>
            <div class="mb-3">
              Total tim 5 : {{ kesimpulan.totalZakatTim5 }}
            </div>
            <div class="mb-3">
              Total tim 6 : {{ kesimpulan.totalZakatTim6 }}
            </div>
            <div class="mb-3">
              Total tim 7 : {{ kesimpulan.totalZakatTim7 }}
            </div>
          </div>
        </div>
      </div>

      <TabelListZakat />
    </div>
  </div>
</template>

<script>
import NavbarZakat from "@/components/NavbarZakat.vue";
import TabelListZakat from "@/components/TabelListZakat.vue";
import axios from "axios";
import { getCookie } from "../cookieUtils.js";
export default {
  name: "ListZakat",
  components: {
    NavbarZakat,
    TabelListZakat,
  },
  data() {
    return {
      kesimpulan: [],
    };
  },
  methods: {
    setKesimpulan(data) {
      this.kesimpulan = data;
    },
  },
  mounted() {
    const token = getCookie("token");
    if (!token) {
      this.$router.push("/login");
    }

    axios
      .get("http://103.155.117.238:5000/input-zakat/search/kesimpulan/", {})
      .then((response) => this.setKesimpulan(response.data.data))
      .catch((error) => console.log(error));
  },
};
</script>

